<template>
  <section id="about">
    <div class="info">
      <div class="info__carousel padding" v-for="item in aboutInfo" :key="item.name">
        <transition-group name="slider" tag="p">
            <span class="info__text" v-for="el in item.text" :key="el.message" v-show="showSlider">
              {{ el.message }}
            </span>
        </transition-group>
      </div>
    </div>
    <div class="slash" @click="nextSlider()"><img src="../assets/media/slash.png" alt="-" class="slash__img"></div>
  </section>
</template>

<script>
export default {
  name: "About",
  data() {
    return {
      aboutInfo: [
        {
          name: 'expert',
          text: [{message: 'Помогаем водителям на месте ДТП с 1998 г.'}, {message: '50 аттестованных экспертов'}, {message: 'Нам доверяют VIP-клиентов: Ингострах, СОГАЗ, Росгосстрах, Согласие, Ренессанс'}, {message: 'Оформляем >200 аварий в день'}]
        },
        {
          name: 'company',
          text: [{message: 'Уникальными страховыми программами, заточенными под требования самых разных клиентов.'}, {message: 'Мобильной службой аварийных комиссаров, покрывающей территорию Москвы и Московской области'}, {message: 'Call-центром, в котором круглосуточно можно получить правовую консультацию.'}, {message: 'высокотехнологичными разработками – уникальными для рынка автострахования.'}]
        },
        {name: 'third', text: [{message: 'Трйтий1'}, {message: 'Третий2'}, {message: 'Третий3'}]}
      ],
      showSlider: true,
    }
  },
  mounted: function () {
    setInterval(() => {
      this.showSlider = !this.showSlider;
      this.nextSlider();
      setTimeout(() => {
        this.showSlider = !this.showSlider
      }, 2000);
    }, 10000);
  },
  methods: {
    nextSlider: function () {
      let arr = [];
      this.aboutInfo.forEach((item, index) => {
        if (index === this.aboutInfo.length - 1) {
          arr[0] = item;
        } else {
          arr[index + 1] = item;
        }
      })
      return this.aboutInfo = arr;
    }
  },
}
</script>

