  transition: all .1ms ease;
  opacity: 0;
}

.slider-enter-to {
  transition: all .5s linear;
  opacity: 100;
}

.slider-leave-active {
  transition: all .5s linear;
  opacity: 0;
  transform: translateX(-100px);
}

