<template>
  <div class="app">
    <h3 v-on:click="showColor = !showColor">изменить цвета</h3>
    <section class="color" v-if="showColor">
      <label for="background">Задний фон</label><input type="color" id="background" v-on:change="changeColorBack" v-model="background">
      <label for="backgroundBlock">Задний фон блоков</label><input type="color" id="backgroundBlock" v-on:change="changeColorBlock()" v-model="colorBlock">
      <label for="text">Цвет текста</label><input type="color" id="text" v-on:change="changeColorText()" v-model="text">
      <label for="alert">Цвет внимания</label><input type="color" id="alert" v-on:change="changeColorAlert" v-model="alert">
      <hr>
      <h4 v-on:click="showColor = false" style="margin-top: 5px">закрыть</h4>
    </section>
    <Up></Up>
    <router-view></router-view>
    <About></About>
    <router-link to="/мой_вариант" class="button">
      <div>Мой вариант</div>
    </router-link>
    <router-link to="/вариант_дизайнера" class="button">
      <div>Вариант дизайнера</div>
    </router-link>
    <router-link to="/просто_картинка" class="button">
      <div>Просто картинка</div>
    </router-link>
    <router-view name="my"></router-view>
<!--    <Services></Services>-->
    <Faq></Faq>
    <Evro></Evro>
    <Contact></Contact>
    <FooterDelta></FooterDelta>
  </div>
</template>


<script>
import Up from "./components/Up";
// import Services from "@/components/Services";
import Faq from "@/components/Faq";
import Evro from "@/components/Evro";
import Contact from "@/components/Contact";
import FooterDelta from "@/components/Footer-delta";
import About from "@/components/About";
export default {
  name: 'App',
  data() {
    return {
      background: '',
      text: '',
      alert: '',
      colorBlock: '',
      showColor: false
    }
  },
  components: {
    About,
    FooterDelta,
    Contact,
    Evro,
    Faq,
    Up,
    // Services
  },
  methods: {
    changeColorAlert() {
      let html = document.getElementById('delta');
      return html.style.setProperty('--atention-color', this.alert);
    },
    changeColorBack() {
      let html = document.getElementById('delta');
      return html.style.backgroundColor = this.background;
    },
    changeColorText() {
      let html = document.getElementById('delta');
      return html.style.setProperty('--text-color', this.text);
    },
    changeColorBlock() {
      let html = document.getElementById('delta');
      return html.style.setProperty('--back-color', this.colorBlock);
    },
  }
}
</script>



