import Vue from 'vue'
import VueRouter from 'vue-router'
import First from '../components/First'
import Expertcall from "@/components/Expertcall";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: First,
    },

    {
        path: '/call',
        name: 'Call',
        component: Expertcall,
        children: [
            {
                path: 'done',
                component: () => import('../components/Done.vue')
            },
            {
                path: 'ab'
            }
        ]
    },
    {
        path: '*',
        name: 'not-existing',
        component: () => import('../components/Notexisting.vue')
    },
    {
        path: '/мой_вариант',
        name: 'MyVariable',
        components: {
            default: First,
            my: () => import('../components/Services')
        }
    },
    {
        path: '/вариант_дизайнера',
        name: 'DesignerVariable',
        components: {
            default: First,
            my: () => import('../components/Syka')
        },
    },
    {
      path: '/просто_картинка',
      name: 'Image',
      components:{
          default: First,
          my: ()=> import('../components/Syka2')
      }
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
    // scrollBehavior(to, from, savedPosition) {
    //     if (savedPosition) {
    //         return savedPosition
    //     } else {
    //         return {x: 0, y: 0}
    //     }
    // },
})
export default router
