<template>
  <section class="expert-call padding">
    <div v-if="show">
      <h3 class="expert__h">Вызов автоэксперта на <br> место ДТП</h3>
      <div class="personal-data">
        <form class="expert__form" id="expert__form">
          <label for="name" class="expert__label">
            ФИО
            <input type="text" id="name" placeholder="Иванов Иван Иваныч" class="expert__input">
          </label>
          <label for="phone" class="expert__label">
            Номер телефона
            <input type="tel" id="phone" class="expert__input" placeholder="+79250123569">
          </label>
          <label for="where" class="expert__label">
            Адрес ДТП <a :href="map">Карта</a>
            <input type="text" id="where" class="expert__input" @focus="getPosition" value=" " autocomplete="nope">
            <p>{{ message }}</p>
          </label>
          <label class="expert__label">
            Способ оплаты
            <label class="expert__label_pay" for="cash"><input type="radio" id="cash" :value="true" name="pay" v-model="cash"> Наличными эксперту</label>
            <label class="expert__label_pay" for="cart"><input type="radio" id="cart" :value="false" name="pay" v-model="cash"> Картой онлайн</label>
          </label>
          <label for="agree">
            <input type="checkbox" id="agree"  :value="true" v-model="agree">
            Я согласен с <a href="#description" class="attention">правилами</a> оформления услуги
          </label>
        </form>
        <div class="button-true" v-if="agree">
          <router-link to="/call/done" v-if="cash">
            <div :class="{button: agree, none: !agree}" @click="showDone()">ОФОРМИТЬ</div>
          </router-link>
          <div :class="{button: agree, none: !agree}" v-else><a href="https://kassa.yandex.ru/payments/">ОПЛАТИТЬ</a></div>
        </div>
        <div class="button-false" v-else>
          <p class="button-false__text">Пожалуйста, подтвердите согласие с правилами оказания услуги </p>
        </div>
      </div>
      <div class="description" id="description">
