        <div class="button"><a href="#expert__form">ПЕРЕЙТИ К ОФОРМЛЕНИЮ</a></div>
      </div>
    </div>
    <router-view></router-view>
  </section>
</template>

<script>
export default {
  name: "Expertcall",
  data() {
    return {
      map: '',
      message: '',
      show: true,
      cash: true,
      agree: false,
    }
  },
  methods: {
    getPosition() {
      const input = document.querySelector('#where');
      input.value = '';

      const options = {
        enableHighAccuracy: true,
        timeout: 5000,
        maximumAge: 0
      }

      if (!navigator.geolocation) {
        this.message = 'Поиск геолокации не поддерживается в вашем браузере.';
      } else {
        this.message = 'Поиск…';
        navigator.geolocation.getCurrentPosition((position) => {
          const latitude = position.coords.latitude;
          const longitude = position.coords.longitude;
          this.message = '';
          input.value = `Latitude: ${latitude} °, Longitude: ${longitude} °`;
          this.map = `https://www.openstreetmap.org/#map=18/${latitude}/${longitude}`;
        }, () => {
          this.message = 'Разрешите определение геолокации';
        }, options);
      }
    },
    showDone() {
      this.show = !this.show;
    },
    setCash() {
      this.cash = !this.cash;
    }
  }
}
</script>

